$backgroundColor: #F1FABD;
$backgroundArticle: #fffbecf0;
$bodyColor: #4c362b;
$bodyFont: "Lexend", sans-serif;
$colorAccent1: #809C3A;
$colorAccent2: #ed670a;
$colorAccent3: #5e7522;
$colorAccent4: #3e4f12;
$headerFont1: "Zilla Slab", serif;
$headerFont2: "Zilla Slab Highlight", serif;
$headerFont3: "League Gothic", sans-serif;

.accent-1 {
  color: $colorAccent1;
}
.accent-2 {
  color: $colorAccent2;
}
.accent-3 {
  color: $colorAccent3;
}
.accent-4 {
  color: $colorAccent4;
}

html, body {
  overscroll-behavior-x: none;
}

.form-check-input:checked {
  background-color: $colorAccent4;
  border-color: $colorAccent2;
}

.btn-primary {
  --bs-btn-bg: #{$colorAccent2};
  --bs-btn-border-color: #{$colorAccent3};
  --bs-btn-hover-bg: #{$colorAccent3};
  --bs-btn-hover-border-color: #{$colorAccent3};
  --bs-btn-active-bg: #{$colorAccent3};
  --bs-btn-active-border-color: #{$colorAccent3};
}
.btn-primary:hover {
  background-color: $colorAccent1;
}

#toTop {
  position: fixed;
  right: 1rem;
  bottom: -5rem;
  opacity:0;
  animation: toTop linear forwards;
  animation-timeline: view();
  animation-range-start: 90vh;
  animation-range-end: 130vh;
}

@keyframes toTop {
 to { 
     opacity: 1;
     bottom:1rem;
     }
}

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
  font-size: 1.2rem;
}

nav {
  font-family: $headerFont1;
  font-weight: 600;
  border-color: $backgroundColor;
  border-top-width: 2px;
  border-bottom-width: 2px;
  border-right-width: 0px;
  border-left-width: 0px;
  border-style: solid;
}

nav a, nav ul.dropdown-menu, nav div.btn-group a:first-child {
  @media (max-width: 768px)
  {
  font-size: 1.2rem;
  letter-spacing: 0;
  }
  @media (min-width: 769px)
  {
  font-size: 1.6rem;
  letter-spacing: 0.1rem;
  }
}

nav, nav a {
  color: $backgroundColor;
  background-color: $colorAccent1;
  text-decoration: none;
  padding-right: 1rem;
}

nav a.current {
  color: #3e4f12;
}

nav a.current:hover {
  color: $colorAccent4;
}

nav a:hover {
  color: $colorAccent4;
}

nav div.btn-group a:first-child {
  font-family: $headerFont1;
  font-weight: 600;
  color: $backgroundColor;
  text-decoration: none;
  text-transform: uppercase;
}

nav ul.dropdown-menu  {
  background-color: $backgroundArticle;
}

nav div.btn-group a.dropdown-item {
  color: $colorAccent4;
  letter-spacing: 0;
}

nav div.btn-group a.dropdown-item:hover {
  color: $backgroundColor;
}

a.dropdown-item {
  letter-spacing: 0;
  text-transform: capitalize;
}
a.dropdown-item:hover {
  color: white;
  background-color: $colorAccent2;
}

a.dropdown-item:focus {
  background-color: $colorAccent4;
}

.dropdown-menu {         
  max-height: 400px;
  overflow-y: auto;
}

body p, ul.list {
  margin-top: 1.4rem;
  margin-bottom: 1.5rem;
  line-height: 2rem;
  font-weight: 400;
}

blockquote {
  padding-left: 2rem;
  font-size: 1.2rem;
  line-height: 1.8rem;
  margin-top: 2rem;
}

main {
  background-color: $backgroundArticle;
}
main a, footer a {
  color: $colorAccent2;
}


h1 {
  @media (max-width: 768px)
    {
    font-size:3rem; 
    line-height: 3rem;
    }
  @media (min-width: 769px)
  {
  font-size: 6rem;
  line-height: 6rem;
  }
}

h1 {
  font-weight: 200;
  font-family: $headerFont3;
  text-transform: uppercase;
  color: $colorAccent1;
  text-shadow: 1px 1px rgba(30, 161, 91, 0.2), -5px -3px rgb(239, 221, 173, 0.5);
}

h1 a {
  text-decoration: none;
  color: $colorAccent1;
}

h1 a:hover {
  color: $colorAccent2;
}

h1 em {
  font-family: $headerFont1;
  text-transform: none;
  letter-spacing: 0;
  font-size: 1.4rem;
}

h2 {
  font-family: $headerFont1;
  font-weight: 600;
  font-size: 1.4rem;
}

h2 i, li i {
  color: $colorAccent3;
}

h3 {
  display: block;
  padding: 1rem;
  background-color: $colorAccent1;
  font-family: $headerFont1;
  color: white;
  font-size: 1.6rem;
  line-height: 1.8rem;
  font-weight: 600;
  letter-spacing: 0.06rem;
  margin-left: -1.5rem;
  box-shadow: rgba(67, 128, 10, 0.15) 1.95px 1.95px 2.6px;
}

h4 {
  font-size: 1.2rem;
}

ul#sub-nav {
  font-family: $headerFont1;
  font-weight: 600;
  color: $bodyColor;
  font-size: 1.4rem;
  line-height: 2.1rem;
  list-style: none;
  padding: 0;
}
ul#sub-nav li:hover, ul#sub-nav li:hover a {
  background-color: $colorAccent2;
  color: $bodyColor;
}

ul#sub-nav li {
  line-height: 2.6rem;
  padding-left: 1rem;
}

ul#sub-nav li a {
  text-decoration: none;
  display: block;
}
ul#sub-nav li a:hover {
  color: white;
}

ul#sub-nav a {
  color: $bodyColor;
}

ul#sub-nav li.current a {
  color: $colorAccent2;
}
ul#sub-nav li.current a:hover {
  color: #4c362b;
}
div#sidebar {
  max-width: 382px;
}

.fs-label {
  font-weight: 600;
  text-transform: uppercase;
}
.fs-input, .fs-textarea {
  font-size: 1.3rem;
  font-weight: 500;
  color: $colorAccent3;
  border-color: #e3e3d3;
  border-style: solid;
  border-width: 3px;
  border-radius: 0px;
  box-shadow: rgba(10, 10, 10, .05) 0 1px 4px 0 inset;
  padding: 0.6rem;
}
.fs-textarea {
  height: 200px;
}

@media (prefers-reduced-motion: no-preference) {
  .animate-zoom {
    max-height: 70vh;
    margin: 0 auto;
    display: block;
    opacity: 0;
    scale: 0.9;
    animation: fade-in linear forwards;
    animation-timeline: view();
    animation-range: 100px 36%;
  }
  @keyframes fade-in {
    to {
      opacity: 1;
      scale: 1;
    }
  }
  .scrolling-fadein {
    -webkit-animation: fadeIn 3s ease-out;
    -moz-animation: fadeIn 3s ease-out;
    -ms-animation: fadeIn 3s ease-out;
    -o-animation: fadeIn 3s ease-out;
    animation: fadeIn 3s ease-out ;
    animation-range: entry 0% contain 25%;
    animation-timeline: view();
  }
  .scrolling-fadeup {
    -webkit-animation: fadeInUp 3s ease-out;
    -moz-animation: fadeInUp 3s ease-out;
    -ms-animation: fadeInUp 3s ease-out;
    -o-animation: fadeInUp 3s ease-out;
    animation: fadeInUp 3s ease-out ;
    animation-range: entry 0% contain 50%;
    animation-timeline: view();
  }
  .scrolling-slideinup {
    -webkit-animation: slideInUp 3s ease-out;
    -moz-animation: slideInUp 3s ease-out;
    -ms-animation: slideInUp 3s ease-out;
    -o-animation: slideInUp 3s ease-out;
    animation: slideInUp 3s ease-out ;
    animation-range: entry 10% contain 50%;
    animation-timeline: view();
  }
}